body {
  font-family: 'Poppins', sans-serif;
  background-color: #0C0C0C;
  color: white;
}

*,
::after,
::before {
  box-sizing: border-box;
}

::-webkit-scrollbar {
  display: none;
}

.row {
  margin: auto;
  max-width: 100%;
}

.maker-url a, .maker-url a:focus, .maker-url a:active {
  text-decoration: none;
  color: whitesmoke;
  border: none;
  background-color: transparent;
  box-shadow: none;
}

.maker-url a:hover {
  cursor: pointer;
  color: #A9C52F;
}

/* .navbar-toggler, .navbar-toggler:focus {
  border: none;
  box-shadow: none;
} */

.navbar {
  background-color: #0C0C0C;
}

.navbar-brand, .nav-item svg, .navbar-brand:hover {
  color: white;
}

.navbar-brand:focus, .nav-link:focus {
  background-color: transparent;
  border: none;
  color: white;
}

.mainHeader {
  margin: auto;
  padding: 10px;
  position: absolute;
  top: 8%;
  width: 100%;
  right: 50%;
  transform: translate(50%, 0);
}

.search-bar {
  width: 80%;
  margin: auto;
  border-radius: 100px;
  padding: 0px;
}

.input-group-text {
  background-color: #222831;
  color: white;
  border: 1px solid black;
  border-right: none;
  border-radius: 100px;
}

.btn-search {
  background-color: #222831;
  border: 1px solid black;
  color: white;
  border-left: none;
  border-radius: 100px;
}

.btn-search:hover,
:focus {
  background-color: #A9C52F;
  border: 1px solid black;
  color: black;
  border-left: none;
  border-radius: 100px;
}

.search-bar input {
  border: 1px solid black;
  background-color: #222831;
  color: white;
  border-left: none;
  border-right: none;
  border-radius: 100px;
}

.search-bar input:focus {
  box-shadow: none;
  background-color: #222831;
  border: 1px solid black;
  border-right: none;
  border-left: none;
  color: white;
}

.random-videos {
  position: absolute;
  top: 12%;
  right: 50%;
  transform: translate(50%, 0%);
  z-index: -18;
}

.display-cards .card{
  background-color: #222831;
  color: white;
}

.display-cards .card:hover {
  box-shadow: 2px 2px #A9C52F;
}

.display-cards .card h5, .display-cards .card p {
  font-size: smaller;
}

.bottom-nav {
  width: 100%;
  background-color: #A9C52F;
  color: #0C0C0C;
  padding: 10px 15px 0px 15px;
  margin: auto;
  position: fixed;
  bottom: 0;
  left: 0;
  box-shadow: 0 -2px 5px rgba(0, 0, 0, 0.1);
  z-index: 20;
}


.video-card .img-thumbnail {
  border: none;
  padding: 0px;
  border-radius: 10px 0px 0px 10px;
  background-color: #222831;
}

.footer {
  position: absolute;
  top: 255%;
  right: 50%;
  transform: translate(50%, 0%);
}

/* #76ABAE */

.video-card .card {
  background-color: #161717;
  border: 1px solid #0C0C0C;
  border-radius: 10px;
  margin: auto;
  color: white;
}

.bottom-nav input[type=range] {
  width: 95%;
  padding: 0px 10px;
  -webkit-appearance: none;
  appearance: none;
  background: transparent;
  cursor: pointer;
  width: 95%;
  --thumb-height: 1.125em;
	--track-height: 0.125em;
	--track-color: rgba(0, 0, 0, 0.2);
	--brightness-hover: 180%;
	--brightness-down: 80%;
	--clip-edges: 0.125em;
}

.bottom-nav input[type=range]:hover,
.bottom-nav input[type=range]:focus {
  border: none;
  box-shadow: none;
}

.btn-play,
.btn-play:focus {
  border: none;
  background-color: #A9C52F;
}

.player-slide {
  display: flex;
  justify-content: center;
}

.login-form {
  width: 50%;
  position: absolute;
  margin: auto;
  top: 25%;
  right: 50%;
  transform: translate(50%, 0%);
}

@media only screen and (max-width: 600px) {
  .navbar {
    padding: 5px 10px;
  }

  .mainHeader {
    margin: auto;
    padding: 10px;
    position: absolute;
    top: 10%;
    width: 100%;
    right: 50%;
    transform: translate(50%, 0);
  }

  .search-bar {
    width: 100%;
    margin: auto;
  }

  .video-card .card-title, .video-card .card-text {
    font-size: small ;
  }

  .video-card .card-body {
    padding: 10px 0px;
  }

  .video-results {
    position: absolute;
    top: 20%;
    transform: translate(50%, 50%);
  }

  .video-results .img-thumbnail {
    border: none;
    padding: 0px;
    border-radius: 4px 4px 0px 0px;
  }
  
  .video-card .card {
    border: 1px solid black;
    border-radius: 5px;
    margin: auto;
  }

  .bottom-nav {
    width: 100%;
    background-color: #A9C52F;
    padding: 10px 15px 0px 15px;
    margin: auto;
    position: fixed;
    bottom: 0;
    left: 0;
    box-shadow: 0 -2px 5px rgba(0, 0, 0, 0.1);
    z-index: 20;
  }

  .video-results {
    position: absolute;
    top: 12%;
    right: 50%;
    transform: translate(50%, 0%);
    z-index: -18;
  }

  .bottom-nav .row .col-lg-12{
    width: 100%;
    margin: auto;
    padding: 0px;
  }

  .bottom-nav .audio-title, .bottom-nav .audio-src {
    font-size: smaller;
  }

  .bottom-nav input[type=range] {
    width: 95%;
    padding: 0px 10px;
    -webkit-appearance: none;
    appearance: none;
    background: transparent;
    cursor: pointer;
    width: 95%;
  }

  .video-results {
    position: absolute;
    top: 12%;
    right: 50%;
    transform: translate(50%, 0%);
  }

  .login-form {
    width: 90%;
    position: absolute;
    margin: auto;
    top: 15%;
    right: 50%;
    transform: translate(50%, 0%);
  }

  .footer {
    position: absolute;
    top: 450%;
    right: 50%;
    padding-bottom: 20px;
    transform: translate(50%, 0%);
  }
}